* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

button {
  text-align: center;
  color: #0d172a;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
  background-color: #fff;
  border: 0 solid #e2e8f0;
  border-radius: 1.5rem;
  padding: 1rem 1.6rem;
  font-family: inherit;
  font-size: 1.1rem;
  font-weight: 600;
  line-height: 1;
  transition: color .1s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
  position: relative;
  left: 15px;
  box-shadow: 0 1px 2px rgba(166, 175, 195, .7);
}

button:disabled, button:disabled:hover {
  color: #fff;
  background-color: #e5e5e5;
  box-shadow: 0 1px 2px rgba(166, 175, 195, .1);
}

button:hover {
  color: #fff;
  background-color: #1e293b;
}

.timer {
  gap: 11px;
  display: flex;
  position: relative;
  top: 15px;
  left: 20px;
}

.field {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.field > .value {
  font-size: 26px;
  font-weight: 600;
  line-height: 1.5;
}

input {
  border: 0 solid #e2e8f0;
  border-radius: 1.5rem;
  padding: 1rem 1.6rem;
  font-family: inherit;
  font-size: 1.1rem;
  font-weight: 600;
  line-height: 1;
  box-shadow: 0 0 6px rgba(166, 175, 195, .7);
}

input:focus {
  outline: 1px solid #ccc;
}

.form {
  width: 450px;
  border-radius: 10px;
  outline: 1px solid #ccc;
  flex-direction: column;
  gap: 1.1rem;
  padding: 40px;
  display: flex;
  position: relative;
  left: 15px;
}

.form > label {
  text-align: center;
  border-radius: 1.5rem;
  flex-direction: row-reverse;
  justify-content: flex-end;
  align-items: center;
  gap: 20px;
  font-family: inherit;
  font-size: 1rem;
  font-weight: 600;
  display: flex;
}

.form > label:nth-child(3) {
  gap: 1.7rem;
}

.form > button {
  left: 0;
}

